import React from 'react';
import { Featured } from './BodyContents/Featured';
import { Fresh } from './BodyContents/Fresh';
import { BestSellers } from './BodyContents/BestSellers';
import { Banner } from './BodyContents/Banner';
import { Review } from './BodyContents/Review';

const bannerOne = {
    bannerImage: "https://images.unsplash.com/photo-1517393435090-3ec2cce47400?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=9608f51dd03bfa6b5fb5f07228e2e1ac&auto=format&fit=crop&w=1062&q=80",
    title: "PRODUCT SALES",
    btnText: "DISCOVER MORE"
};

const bannerTwo = {
    bannerImage: "https://images.unsplash.com/photo-1507842217343-583bb7270b66?ixlib=rb-0.3.5&s=5d1e7481e42e2ff1a261966aee5c2f9d&auto=format&fit=crop&w=1153&q=80",
    title: "SUBSCRIBE AND GET OFFERS!",
    btnText: "SUBSCRIBE"
};

export class BodyContent extends React.Component {
    render() {
        return (
            <div className="body-content">
                <Featured />
                <hr />
                <BestSellers />
                <Banner image={bannerOne.bannerImage} title={bannerOne.title} btnText={bannerOne.btnText}/>
                <Fresh />
                <Banner image={bannerTwo.bannerImage}  title={bannerTwo.title} btnText={bannerTwo.btnText}/>
                <Review />
            </div>
        );
    }
}