import React from 'react';

const menuItem = [
    {title: 'Home', icon: 'home', href: "#"},
    {title: 'Books', icon: 'book', href: "#"},
    {title: 'Stationery', icon: 'business_center', href: "#"},
    {title: 'Utensils', icon: 'free_breakfast', href:"#"}
];

export class Aside extends React.Component {
    render() {
        const menuList = menuItem.map((item) => 
            <li className="menu-item"><a href={item.href} className="ripple"><i className="material-icons">{item.icon}</i>{item.title}</a></li>
        );
        return (
            <div className="aside">
                <div className="aside-title">
                    <h3>MENU</h3>
                </div>
                <div className="menu-items">
                    <ul>
                        {menuList}
                    </ul>
                </div>
            </div>
        );
    }
}