import React from 'react';
import { BodyContent } from './BodyContent';

const bodyMastTitle = <span>DISCOVER AND SHOP <br/> THE TREND</span>;

export class Body extends React.Component {
    render() {
        return (
            <div className="body">
                <div className="body-mast">
                    <div>
                        <h1>{bodyMastTitle}</h1>
                    </div>
                    <div className="shop-now-wrapper">
                        <button className="ripple">SHOP NOW</button>
                    </div>
                </div>
                <BodyContent />
            </div>
        );
    }
}