import React from 'react';

const featured = [
    {
        title: "Five Subject Notebook",
        imageUrl: "http://cdn.shopify.com/s/files/1/2340/3311/products/FGN2-C-FRONT_f96163de-d3c7-417d-ac98-4988ef657b54_large.jpg?v=1511161170"
    },
    {
        title: "Dark Theme Notebook",
        imageUrl: "https://cdn.shopify.com/s/files/1/2340/3311/products/FN2E-SIDE_large.jpg?v=1517644227"
    },
    {
        title: "Flap Notebook",
        imageUrl: "http://cdn.shopify.com/s/files/1/2340/3311/products/LBN2-B-FRONT_large.jpg?v=1511161168"
    },
    {
        title: "Functinal Notebook 5",
        imageUrl: "http://cdn.shopify.com/s/files/1/2340/3311/products/FN2M-FRONT_large.jpg?v=1517644699"
    }
];

export class Featured extends React.Component {
    render() {
    const featuredList = featured.map((features) => (
        <li className="section-content-item">
            <img src={features.imageUrl} />
            {features.title}
        </li>
    ));
        return (
            <div id="featured" className="featured section">
                <div className="section-title">
                    Featured Products
                </div>
                <div className="section-content">
                    <ul>
                        {featuredList}
                    </ul>
                </div>
            </div>
        );
    }
}