import React from 'react';

export class Review extends React.Component {
    render() {
        return (
            <div className="review">
            <div className="section-title">
                Review
            </div>
                <div className="textarea-wrapper">
                    <textarea autoComplete="false" autoCorrect="false" spellCheck="false" placeholder="Please Write a Review .... &#x1F601;"></textarea>
                </div>
                <div className="button-wrapper">
                    <button className="ripple">SEND MESSAGE</button>
                </div>
            </div>
        );
    }
}