import React from 'react';

const number = 7205929025;
const title = "ShopIt";

export class Header extends React.Component {
    render() {
        return (
            <div className="header">
                <div className="header-top">
                    <div className="header-top-content">
                        <strong>Contact Us At : </strong>{number}
                    </div>
                </div>
                <div className="header-mast">
                    <div className="cart-wrapper">
                        <button className="ripple user"><i className="material-icons">person</i></button>
                        <button className="ripple cart">
                            <span className="cart-counter"><i className="material-icons">shopping_cart</i></span>
                            <span>CART</span>
                        </button>
                    </div>
                    <div className="header-title">
                        {title}
                    </div>
                </div>
            </div>
        );
    }
}