import React from 'react';

const infos = ['About Us', 'Blog', 'Contact Us'];
const mores = ['Shipping and Returns', 'Terms of Use', 'Privacy Policy'];

export class Footer extends React.Component {
    render() {
        const infoList = infos.map((item) => 
            <li>{item}</li>
        );
        const moreList = mores.map((item) => 
            <li>{item}</li>
        );
        return (
            <div className="footer">
                <div className="footer-body">
                    <div className="footer-info">
                        <span>
                            INFO
                        </span>
                        <ul>
                            {infoList}
                        </ul>
                    </div>
                    <div className="footer-more">
                        <span>
                            MORE
                        </span>
                        <ul>
                            {moreList}
                        </ul>
                    </div>
                </div>
                <div className="copyright">
                    <span>
                        COPYRIGHT Â© 2018 SHOPIT. ALL RIGHTS RESERVED.
                    </span>
                </div>
            </div>
        );
    }
}